import { CommonModule } from '@angular/common';
import { NgModule, ModuleWithProviders } from '@angular/core';

import { FarrisCommonModule } from '@farris/ui-common';

import { NotifyComponent } from './notify.component';
import { NotifyContainerComponent } from './notify-container.component';
import { NotifyService } from './notify.service';
import { LocaleModule } from '@farris/ui-locale';

@NgModule({
    imports: [
        CommonModule,
        FarrisCommonModule,
        LocaleModule
    ],
    declarations: [ NotifyComponent, NotifyContainerComponent],
    providers: [
        NotifyService
    ],
    exports: [
        NotifyComponent,
        NotifyContainerComponent
    ],
    entryComponents: [NotifyContainerComponent]
})
export class NotifyModule {
    static forRoot(): ModuleWithProviders {
        return {
            ngModule: NotifyModule,
            providers: [NotifyService]
        };
    }
}
